.work {
  padding: 50px 0;
  background: #fff; }

.work_technologies {
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin: 25px 0 5px; }
  .work_technologies li {
    list-style: none;
    margin-right: 15px; }
    .work_technologies li:first-child::before {
      content: '<';
      padding-right: 10px;
      font-size: 22px;
      font-weight: 700;
      color: #00bcd4; }
    .work_technologies li:last-child::after {
      content: '/>';
      padding-left: 10px;
      font-size: 22px;
      font-weight: 700;
      color: #00bcd4; }
  .work_technologies button {
    border: none;
    background: none;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 22px;
    text-shadow: 1px 1px 3px #aaa;
    cursor: pointer; }
    .work_technologies button:hover {
      color: #00bcd4; }
  .work_technologies .active {
    color: #00bcd4;
    border-bottom: 2px solid #00bcd4; }

.work_content {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 4%; }

.work_title {
  margin-bottom: 20px; }
  .work_title h2:before {
    background: #000; }

.work_box {
  display: relative;
  margin: 20px 30px;
  background-color: #f9fcff;
  background-image: linear-gradient(147deg, #f9fcff 0%, #dee4ea 74%);
  border-radius: 10px;
  border: 2px solid #d3d3d3;
  box-shadow: 0 5px 20px #979797;
  overflow: hidden;
  width: 350px; }

.work_img img {
  width: 100%;
  height: 250px;
  object-fit: cover; }

.work_date {
  padding: 5px 0;
  font-size: 0.8em;
  text-align: right; }
  .work_date span {
    text-decoration: underline; }

.work_description {
  display: flex;
  flex-direction: column;
  padding: 5px 20px;
  min-height: 200px;
  color: #3b3a3a; }
  .work_description p {
    min-height: 85px;
    font-size: 14px;
    font-weight: 500;
    text-align: justify; }
  .work_description span {
    margin: 10px 5px 10px 0; }

.work_tags {
  margin-bottom: 10px; }

.tag {
  background: #888;
  filter: contrast(80%);
  opacity: 0.8;
  border-radius: 50px;
  font-size: 12px;
  margin: 0;
  color: #fff;
  padding: 2px 10px;
  text-transform: uppercase;
  cursor: pointer; }
  .tag.react-color {
    background: #22c2e2; }
  .tag.js-color {
    background: #d4b830; }
  .tag.css-color {
    background: #0e57a7; }
  .tag.html-color {
    background: #e44d26; }
  .tag.other-color {
    background: #cd5b9f; }

.work_links {
  display: flex;
  justify-content: space-between;
  margin: 10px 20px 15px 20px; }
  .work_links button {
    padding: 10px 20px;
    text-decoration: none;
    font-weight: 700;
    letter-spacing: 1.5px;
    background-color: #000000;
    background-image: linear-gradient(315deg, #000000 0%, #414141 74%);
    border: none;
    border-radius: 5px;
    cursor: pointer; }
    .work_links button a {
      color: #fff;
      text-decoration: none; }
    .work_links button:hover {
      box-shadow: 1px 1px 5px #63b7c4, -1px -1px 5px #63b7c4; }
      .work_links button:hover a {
        color: #63b7c4; }

.shine figure {
  position: relative;
  cursor: pointer;
  filter: grayscale(70%); }
  .shine figure:hover {
    filter: grayscale(0%); }

.shine figure::before {
  position: absolute;
  top: 0;
  left: -75%;
  z-index: 2;
  display: block;
  content: '';
  width: 50%;
  height: 100%;
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  -webkit-transform: skewX(-25deg);
  transform: skewX(-25deg); }

.shine figure:hover::before {
  -webkit-animation: shine .75s;
  animation: shine .75s; }

@-webkit-keyframes shine {
  100% {
    left: 125%; } }
@keyframes shine {
  100% {
    left: 125%; } }
@media (max-width: 991px) {
  .work_box {
    width: 450px; } }
